const preloads = new Map<string, Promise<void>>()

// https://developer.mozilla.org/en-US/docs/Web/API/HTMLLinkElement/fetchPriority
declare global {
  interface HTMLLinkElement {
    fetchPriority: "high" | "low" | "auto"
  }
}

type Options = {
  as?: string
  type?: string
  fetchPriority?: "high" | "low" | "auto"
}

export const preload = (resource: string, opts: Options = {}) => {
  opts = {
    as: "fetch",
    fetchPriority: "low",
    ...opts,
  }

  // https://developer.mozilla.org/en-US/docs/Web/HTML/Link_types/preload#cors-enabled_fetches
  if (["fetch", "font"].includes(opts.as!)) (opts as any).crossOrigin = ""

  if (!preloads.has(resource))
    preloads.set(
      resource,
      new Promise<void>((res, rej) => {
        const link = document.createElement("link")
        link.rel = "preload"
        link.href = resource
        link.onerror = rej
        link.onload = () => res()
        Object.assign(link, opts)
        document.head.prepend(link)
      })
    )
  return preloads.get(resource)!
}
