import type { captureException } from "@sentry/browser"

type Sentry = {
  captureException: typeof captureException
}

let sentry: Promise<Sentry>

export const isSentryEnabled = import.meta.env.VITE_SENTRYJS_DSN

const onError = (e: ErrorEvent) => {
  initSentry(e.error)
}
const onUnhandledRejection = (e: PromiseRejectionEvent) => initSentry(e.reason)
const logError = (error: Error) => sentry.then((Sentry) => Sentry.captureException(error))

if (isSentryEnabled) {
  window.addEventListener("error", onError)
  window.addEventListener("unhandledrejection", onUnhandledRejection)
}

function initSentry(error: Error) {
  if (!sentry) {
    sentry = Promise.resolve()
      .then(() =>
        Promise.all([import("./sentry.min")]).then(
          ([{ init, captureException, BrowserTracing }]) => {
            init({
              environment: import.meta.env.MODE,
              release: import.meta.env.VERSION,
              dsn: import.meta.env.VITE_SENTRYJS_DSN,
              integrations: [new BrowserTracing()],
              tracesSampleRate: 1.0,
            })

            return { captureException }
          }
        )
      )
      .then((sentry) => {
        window.removeEventListener("error", onError)
        window.removeEventListener("unhandledrejection", onUnhandledRejection)
        return sentry
      })

    logError(error)
  }
}
