// https://docs.google.com/spreadsheets/d/14qZEHG4ic38ilf2Dn11RujojhBvqLwl-_649sd8QKNc/edit#gid=0
export type Event =
  /* Start App events */
  | "app_opened"
  | "terms_of_use_agree_clicked"
  | "live_camera_clicked"
  | "camera_permission"
  | "face_analysis_started"
  | "face_analysis_finished"
  | "upload_photo_clicked"
  | "photo_uploaded"
  | "close_app"

  /* Main menu Events */
  | "main_left_section_clicked"
  | "main_color_analysis"
  | "main_color_analysis_read_more"

  /* Filter Section Events */
  | "filter_brand_selected"
  | "filter_info_clicked"
  | "filter_info_closed"
  | "filter_applied"
  | "filter_deleted"

  /* Product Section Events */
  | "product_category_choice_clicked"
  | "product_applied"
  | "product_removed"

  /* Photo Editing Events */
  | "photo_before_after_clicked"
  | "photo_undo_clicked"
  | "photo_redo_clicked"
  | "photo_show_clicked"
  | "photo_show_hide_product"
  | "photo_show_delete_product"
  | "photo_applied_products_hide"

  /* Book a demo popup events */
  | "popup_book_demo_date_picked"
  | "popup_book_demo_closed"

  /* Cart Events */
  | "add_to_cart_clicked"
  | "add_to_cart_clicked_list"

/* Makeup Transfer Events */
// unimplemented
// | "makeup_add_photo_clicked"
// | "makeup_face_analysis_started"
// | "makeup_face_analysis_finished"
// | "try_on_cosmetics_clicked"
// | "makeup_back_clicked"

export type Props = string | string[] | number | number[] | boolean | { [key: string]: Props }

export interface Client {
  log(event: Event, props?: Props): void
}

export const create = (loader: () => Promise<Client>) => {
  let p: Promise<Client>

  const client: Client = {
    log: (...args) => {
      ;(p ??= loader()).then((c) => c.log(...args))
    },
  }

  return client
}
