import { type Client, create } from "./client"

let mixpanel: Client | null = null

const isEnabled = import.meta.env.VITE_MIXPANEL_TOKEN
const MIXPANEL_APP_NAME = "tint"

if (isEnabled)
  mixpanel = create(() =>
    import("./mixpanel.min").then(({ init }) => {
      const mp = init(
        import.meta.env.VITE_MIXPANEL_TOKEN,
        { api_transport: "sendBeacon", disable_cookie: true },
        MIXPANEL_APP_NAME
      )
      mp.identify()

      const log: Client["log"] = (eventName, eventProps = {}) => {
        if (typeof eventProps !== "object" || eventProps === null) eventProps = { eventProps }
        mp.track(eventName, eventProps)
      }

      return { log }
    })
  )

export default mixpanel
