import { derived, writable } from "svelte/store"
import { match, resolve } from "./utils"

export type Location = {
  pathname: string
  query: Record<string, any>
}

export type PathOrLocation = string | Location

export type History = ReturnType<typeof createMemoryHistory>

export const createMemoryHistory = (init: PathOrLocation) => {
  const history = writable({
    index: 0,
    locations: [toLocation(init)],
  })

  const goto = (location: PathOrLocation, { replace = false } = {}) =>
    history.update(({ index, locations }) => {
      location = toLocation(location)

      const { pathname: current } = locations[index]
      const { pathname: next } = location

      location.pathname = resolve(current, next)

      if (replace) index -= 1
      if (index < locations.length - 1) locations = locations.slice(0, index + 1)

      index = locations.push(location) - 1

      return { index, locations }
    })

  const push = (location: PathOrLocation) => goto(location)
  const replace = (location: PathOrLocation) => goto(location, { replace: true })

  const go = (steps: number) =>
    history.update(({ index, locations }) => {
      index = Math.max(0, Math.min(index + steps, locations.length - 1))
      return { index, locations }
    })

  const back = () => go(-1)
  const forward = () => go(1)

  const { subscribe } = derived(history, ({ index, locations }) => ({ location: locations[index] }))

  return {
    push,
    replace,
    go,
    forward,
    back,
    subscribe,
    match,
  }
}

const toLocation = (location: PathOrLocation) => {
  if (typeof location === "string") {
    const [pathname, search] = location.split("?")
    const query = Object.fromEntries(new URLSearchParams(search).entries())
    return { pathname, query }
  }
  const { pathname, query = {} } = location
  return { pathname, query }
}
