import data from "@banuba/webar/BanubaSDK.data?url"
import simd from "@banuba/webar/BanubaSDK.simd.wasm?url"
import wasm from "@banuba/webar/BanubaSDK.wasm?url"

const config = {
  clientToken: import.meta.env.VITE_SDK_TOKEN,
  devicePixelRatio: 1,
  locateFile: {
    "BanubaSDK.data": data,
    "BanubaSDK.wasm": wasm,
    "BanubaSDK.simd.wasm": simd,
  },
}

export const setClientToken = (clientToken: string) =>
  (config.clientToken = clientToken ?? config.clientToken)

export default config
