<script lang="ts">
  import Floating from "../Floating"
  import { cx } from "../utils"

  type FloatingProps = Floating["$$prop_def"]

  export let anchor: NonNullable<FloatingProps["anchor"]>
  export let placement: FloatingProps["placement"] = "bottom"
  export let offset: FloatingProps["offset"] = [0, 0]
  export let delay = 2000

  let floating: Floating

  $: classes = cx($$restProps.class, "tint-popup")
</script>

<Floating
  bind:this={floating}
  {anchor}
  {placement}
  {offset}
  on:open={() => setTimeout(() => floating?.close(), delay)}
>
  <div class={classes}>
    <slot />
  </div>
</Floating>

<style lang="postcss">
  .tint-popup {
    @apply flex shrink-0 items-center rounded-lg bg-[#38383c] px-4 py-2 text-sm text-white;
  }
</style>
