<!-- Lightweight alternative to svelte-inline-svg npm package -->
<script context="module" lang="ts">
  type SVG = {
    attributes: Record<string, any>
    innerHTML: string
  }

  type LoadOptions = {
    priority: RequestInit["priority"]
  }

  const cache = new Map<string, Promise<SVG>>()

  const load = (src: string, opts: Partial<LoadOptions> = {}) => {
    if (!cache.has(src))
      cache.set(
        src,
        fetch(src, opts)
          .then((response) => {
            if (!response.ok) throw new Error(`${response.status}: Failed to fetch SVG from ${src}`)
            return response.text()
          })
          .then((text) => new DOMParser().parseFromString(text, "text/xml").querySelector("svg")!)
          .then((svg) => {
            const attributes = Object.fromEntries(
              Array.from(svg.attributes, ({ name, value }) => [name, value])
            )
            const innerHTML = svg.innerHTML
            return { attributes, innerHTML }
          })
      )

    return cache.get(src)!.catch((error) => {
      cache.delete(src)
      throw error
    })
  }
</script>

<script lang="ts">
  import { afterUpdate, createEventDispatcher, onMount } from "svelte"

  export let src: string
  export let fetchPriority: LoadOptions["priority"] = undefined

  let mounted = false

  let svg: SVG = {
    attributes: { width: 0, height: 0 },
    innerHTML: "",
  }

  const dispatch = createEventDispatcher<{ load: SVGElement; error: Error }>()

  let self: SVGElement

  $: mounted &&
    load(src, fetchPriority ? { priority: fetchPriority } : {})
      .then((s) => (svg = s))
      .catch((e) => dispatch("error", e))

  onMount(() => (mounted = true))

  afterUpdate(() => self.children.length && dispatch("load", self))
</script>

<svg bind:this={self} xmlns="http://www.w3.org/2000/svg" {...svg.attributes} {...$$restProps}>
  {@html svg.innerHTML}
</svg>

<style lang="postcss">
  svg {
    @apply inline-block;
  }
</style>
