import { getContext as _getContext, setContext as _setContext } from "svelte"

type Route = {
  path: string
  unimplemented?: boolean
}

type Context = {
  routes: Route[]
  demo?: boolean
  close: () => void
}

const key = Symbol("layout")

export const setContext = (ctx: Context) => _setContext(key, ctx)
export const getContext = () => _getContext<Context>(key)
