<script context="module" lang="ts">
  type Variant =
    | "add-to-cart"
    | "added-to-cart"
    | "advisor"
    | "analysis"
    | "arrow-down"
    | "arrow-left"
    | "arrow-right-to-line"
    | "arrow-right"
    | "arrow-small-up"
    | "arrow-up"
    | "back"
    | "bag"
    | "before"
    | "best-match"
    | "book-a-demo"
    | "skin-care"
    | "checkmark"
    | "checkmark-xs"
    | "checkmark-sm"
    | "checkmark-md"
    | "chevron-down"
    | "chevron-left"
    | "chevron-right"
    | "chevron-small-down"
    | "chevron-small-left"
    | "chevron-small-right"
    | "chevron-small-up"
    | "chevron-xs-down"
    | "chevron-xs-up"
    | "chevron-up"
    | "close-circle"
    | "close-small"
    | "close-xs"
    | "close"
    | "delete"
    | "dots"
    | "forward"
    | "gift-selection"
    | "hide"
    | "info"
    | "info-circle"
    | "logo"
    | "makeup-transfer"
    | "menu"
    | "plus-in-circle"
    | "recommend-xs"
    | "recommend-m"
    | "selector"
    | "show"
    | "tag-sale"
    | "full"
    | "applied"

  export type { Variant }

  export const icons = Object.fromEntries(
    Object.entries(import.meta.glob("./svg/*", { eager: true, as: "url" })).map(([path, url]) => [
      path.replace(/\.\/svg\/(.*?)\.\w+$/, "$1"),
      url,
    ])
  )
</script>

<script lang="ts">
  import InlineSVG from "./InlineSVG.svelte"
  import { getBase } from "./context"

  const base = getBase()

  // https://medium.com/geekculture/type-safe-mutual-exclusivity-in-svelte-component-props-3cc1cb871904
  type $$Props =
    | {
        class?: string
        variant: Variant
        src?: never
      }
    | {
        class?: string
        variant?: never
        src: string
      }

  export let variant = ""
  export let src = ""

  $: _src = src || (base ? `${base}${variant}.svg` : icons[variant])
</script>

<InlineSVG
  {...$$restProps}
  src={_src}
  on:error={() =>
    // fallback
    variant && base && (_src = icons[variant])}
/>
