import { get, writable } from "svelte/store"

export type ImageSource =
  | null
  | {
      type: "webcam"
      data?: Blob
    }
  | {
      type: "photo"
      data: Blob
    }

export const create = () => {
  const { set, update, subscribe } = writable<ImageSource>(null)

  const isEmpty = () => !get({ subscribe })
  const isWebcam = () => get({ subscribe })?.type === "webcam"
  const isPhoto = () => get({ subscribe })?.type === "photo"

  return {
    set,
    update,
    subscribe,

    isEmpty,
    isWebcam,
    isPhoto,
  }
}

export default create()
