import { defineConfig } from "@lib/router"
import { Paths } from "@lib/router"
import source from "./best-match/stores/image-source"
import * as sca from "./face-analysis/state"
import { isGift, isSourceChanged, setGift, setSourceChanged } from "./gift"
import * as terms from "./terms/state"

export { setContext as setLayoutContext } from "./_layout"
export { setContext as setAppContext } from "./context"

export type Settings = Partial<{
  terms: boolean
  imageSourceSelection: boolean
  faceAnalysis: boolean

  bestMatch: boolean
  makeupTransfer: boolean
  giftSelection: boolean
  beautyAdvisor: boolean
  bookADemo: boolean
  skinCare: boolean
}>

export default (settings: Settings) =>
  defineConfig([
    {
      action: () => {
        import("@lib/webar").then(({ Analysis, Beauty }) =>
          Promise.resolve(settings.faceAnalysis ? Analysis.preload() : null).then(() => {
            if (source.isWebcam()) Beauty.preload("online")
            else if (source.isPhoto()) Beauty.preload("offline")
            else Beauty.preload()
          })
        )
      },
      children: [...auxiliary(settings), ...app(settings)],
    },
  ])

export const auxiliary = (settings: Settings) =>
  defineConfig([
    settings.terms && {
      path: Paths.Terms,
      action: () => import("./terms"),
    },
    settings.imageSourceSelection && {
      path: Paths.ImageSource,
      action: () => import("./image-source"),
    },
    settings.faceAnalysis && {
      path: Paths.FaceAnalysis,
      action: () => import("./face-analysis"),
    },
  ])

export const app = (settings: Settings) =>
  defineConfig([
    {
      action: () => import("./_layout"),
      children: defineConfig([
        settings.bestMatch && {
          path: Paths.BestMatch,
          action: async (ctx) => {
            if (settings.terms && !terms.isAccepted())
              return {
                redirect: {
                  pathname: Paths.Terms,
                  query: { onComplete: ctx.location.pathname },
                },
              }

            if (settings.imageSourceSelection && source.isEmpty()) {
              return {
                redirect: {
                  pathname: Paths.ImageSource,
                  query: { onComplete: ctx.location.pathname },
                },
              }
            }

            if (isGift() && isSourceChanged()) {
              return import("./image-source")
            }

            if (settings.faceAnalysis && sca.isEmpty() && isSourceChanged()) {
              return import("./face-analysis")
            }

            setGift(false)

            if (settings.faceAnalysis && sca.isEmpty() && !isSourceChanged())
              return {
                redirect: {
                  pathname: Paths.FaceAnalysis,
                  query: { onComplete: ctx.location.pathname },
                },
              }

            setSourceChanged(false)

            return import("./best-match")
          },
        },
        settings.makeupTransfer && {
          path: Paths.MakeupTransfer,
        },
        settings.giftSelection && {
          path: Paths.GiftSelection,
          action: async () => {
            setGift(true)

            if (settings.faceAnalysis && sca.isEmpty()) {
              setSourceChanged(true)

              return import("./face-analysis/gift")
            }

            if ((settings.imageSourceSelection && source.isEmpty()) || !isSourceChanged()) {
              return import("./image-source/gift")
            }

            return import("./best-match")
          },
        },
        settings.beautyAdvisor && {
          path: Paths.BeautyAdvisor,
        },
        settings.skinCare && {
          path: Paths.SkinCare,
          action: (ctx) => {
            if (settings.terms && !terms.isAccepted())
              return {
                redirect: {
                  pathname: Paths.Terms,
                  query: { onComplete: ctx.location.pathname },
                },
              }

            if (settings.imageSourceSelection && source.isEmpty()) {
              return {
                redirect: {
                  pathname: Paths.ImageSource,
                  query: { onComplete: ctx.location.pathname },
                },
              }
            }

            if (isGift() && isSourceChanged()) {
              return import("./image-source")
            }

            if (settings.faceAnalysis && sca.isEmpty() && isSourceChanged()) {
              return import("./face-analysis")
            }

            setGift(false)

            if (settings.faceAnalysis && sca.isEmpty() && !isSourceChanged())
              return {
                redirect: {
                  pathname: Paths.FaceAnalysis,
                  query: { onComplete: ctx.location.pathname },
                },
              }

            setSourceChanged(false)

            return import("./best-match")
          },
        },
        settings.bookADemo && {
          path: Paths.BookADemo,
        },
      ]),
    },
  ])

export const navigation = (settings: Settings) => {
  const [{ children }] = app(settings)

  return children!.map(({ path, action }) => ({
    path: path!,
    unimplemented: !action,
  }))
}

export const defaultNavigationFlow = (settings: Settings, entry?: string) => {
  const pages = [
    settings.terms && !terms.isAccepted() && "terms",
    settings.imageSourceSelection && source.isEmpty() && "image-source",
    settings.faceAnalysis && sca.isEmpty() && "face-analysis",
    entry,
  ].filter(Boolean) as string[]

  return {
    initial: () => pages[0],
    next: (to: string) => {
      if (to.startsWith("/")) to = to.slice(1)

      const idx = pages.findIndex((page) => page === to)
      return idx !== -1 ? pages[idx + 1] : undefined
    },
    preload: async (page: string) => {
      await import(`./${page}/index.ts`)
    },
  }
}
