<script context="module" lang="ts">
  import type { IconVariant } from "@lib/elements"
  import { Paths, loading } from "@lib/router"
  import { isMobileDevice } from "@lib/utils/is-mobile-device"
  import { cart } from "@pages/best-match/stores"
  import { pickADate, showAd } from "@pages/demo"
  import { completed } from "../face-analysis/state"

  type Route = {
    path: string
    title: string
    icon: IconVariant
    active: boolean
    unimplemented: boolean
  }

  const RouteSettingByPath: Record<Route["path"], Pick<Route, "title" | "icon">> = {
    [Paths.BestMatch]: {
      title: "Best for you",
      icon: "best-match",
    },
    [Paths.MakeupTransfer]: {
      title: "Makeup transfer",
      icon: "makeup-transfer",
    },
    [Paths.GiftSelection]: {
      title: "Gift selection",
      icon: "gift-selection",
    },
    [Paths.PersonalGuidance]: {
      title: "Personal guidance",
      icon: "analysis",
    },
    [Paths.BeautyAdvisor]: {
      title: "Beauty advisor",
      icon: "advisor",
    },
    [Paths.BookADemo]: {
      title: "Book a demo",
      icon: "book-a-demo",
    },
    [Paths.SkinCare]: {
      title: "Skin care",
      icon: "skin-care",
    },
  }

  export type { Route }
</script>

<script lang="ts">
  import type { SvelteComponent } from "svelte"
  import { createEventDispatcher } from "svelte"
  import { analytics } from "@lib/analytics"
  import { Icon, NavItem, Navbar } from "@lib/elements"
  import Popup from "@lib/elements/Popup/Popup.svelte"
  import { useRouter } from "@lib/router"
  import { getContext } from "./context"

  const router = useRouter()
  const ctx = getContext()
  const dispatch = createEventDispatcher<{ demo: void }>()

  const items: Record<number, SvelteComponent> = {}

  let routes = ctx.routes
    .map((route) => {
      const setting = RouteSettingByPath[route.path as keyof typeof RouteSettingByPath]
      return setting
        ? {
            unimplemented: true,
            ...route,
            ...setting,
            active: false,
          }
        : null
    })
    .filter(Boolean) as Route[]

  $: routes = routes.map((route) => ({
    ...route,
    active: router.match($router.location, route.path),
  }))
</script>

<div>
  <Navbar let:close class={$completed || !$loading ? "" : "pointer-events-none"}>
    <Icon slot="logo" variant="logo" />
    <ul>
      {#each routes as route, idx}
        {@const implemented = !route.unimplemented}
        {@const bestMatch = route.path === Paths.BestMatch}
        {@const bookADemo = route.path === Paths.BookADemo}
        <li class:bookADemo>
          <NavItem
            icon={route.icon}
            active={route.active}
            bind:this={items[idx]}
            on:click={() => {
              if (bookADemo) pickADate()

              if (ctx.demo && !bestMatch && !bookADemo) showAd()
              else if (implemented && !bookADemo) router.push(route.path)

              close()
            }}
            on:click={() => analytics.main_left_section_clicked({ section_name: route.title })}
          >
            {route.title}
          </NavItem>
          {#if route.unimplemented && !ctx.demo}
            <Popup anchor={items[idx]} placement="bottom-start" offset={[2, 0]}>Coming soon</Popup>
          {/if}
          {#if ctx.demo && !bestMatch && !bookADemo}
            <Popup anchor={items[idx]} placement="bottom-start" offset={[2, 0]}>Book a demo</Popup>
          {/if}
        </li>
      {/each}
      {#if isMobileDevice()}
        {#if $cart.length}
          <li class="cart">
            <NavItem as="button" icon="bag" on:click={() => cart.redirect()}>Cart</NavItem>
          </li>
        {/if}
        <li class="close">
          <NavItem as="button" icon="close-circle" on:click={() => (ctx.close(), close())}>
            Close app
          </NavItem>
        </li>
      {/if}
    </ul>
  </Navbar>
  <slot />
</div>

<style lang="postcss">
  div {
    @apply relative z-10 flex h-full w-full xl:h-[48.4375rem] xl:w-[75rem] xl:rounded-xl;

    & > :global(:not(:first-child)) {
      @apply min-w-0;
    }
  }

  ul {
    @apply mb-auto flex grow flex-col gap-2;
  }

  li {
    @apply overflow-hidden;

    &.cart {
      @apply relative;

      &::before {
        @apply absolute top-[0.125rem] left-[1.625rem] block h-3 w-3 rounded-full bg-[#ff2d55] content-[""];
      }
    }

    &:not(.bookADemo) + .close {
      @apply mt-auto;
    }
  }

  .close {
    @apply xl:hidden;
  }

  .bookADemo {
    @apply mt-auto;
  }
</style>
