import { type Readable, derived, writable } from "svelte/store"
import type { Store as AppliedProductsStore } from "./applied-products"
import type { Product, Sku, Vto } from "./types"

type VtoProduct = Omit<Product, "skus"> & {
  swatches: Swatch[]
  visible: boolean
}

type Swatch = {
  id: Sku["id"]
  color: Vto["color"]
  active: boolean
  vto: Vto
}

export const create = (appliedProducts: AppliedProductsStore) => {
  const hidden = writable<VtoProduct["id"][]>([])
  const show = (product: VtoProduct) => {
    hidden.update(($hidden) => $hidden.filter((id) => id !== product.id))
  }
  const hide = (product: VtoProduct) => {
    hidden.update(($hidden) => $hidden.concat([product.id]))
  }

  const swatchByProduct = writable(new Map<Product["id"], Swatch["id"]>())
  const swatch = (product: VtoProduct, swatch: Swatch) => {
    swatchByProduct.update((map) => map.set(product.id, swatch.id))
  }

  const isComparing = writable(false)
  const compare = () => {
    isComparing.update(($isComparing) => !$isComparing)
  }

  const items: Readable<VtoProduct[]> = derived(
    [appliedProducts, hidden, swatchByProduct],
    ([$appliedProducts, $hidden, $swatchByProduct]) => {
      return $appliedProducts.map(({ skus, ...product }) => ({
        ...product,
        swatches: skus.map(({ id, vto }) => {
          const activeSwatchId = $swatchByProduct.get(product.id) ?? skus[0].id

          const color = vto2rgb(vto.color)
          const active = id === activeSwatchId

          return { id, color, active, vto }
        }),
        visible: !$hidden.includes(product.id),
      }))
    }
  )

  const { subscribe } = derived(
    [appliedProducts, isComparing, items, hidden],
    ([, $isComparing, $items, $hidden]) => ({
      canUndo: appliedProducts.canUndo(),
      canRedo: appliedProducts.canRedo(),
      isComparing: $isComparing,
      categories: new Set($items.map(({ type }) => type)),
      items: $items,
      hidden: $hidden,
    })
  )

  return {
    subscribe,

    show,
    hide,
    swatch,
    clear: () => hidden.set([]),

    compare,

    add: appliedProducts.add,
    remove: appliedProducts.remove,
    undo: appliedProducts.undo,
    redo: appliedProducts.redo,
  }
}

function vto2rgb(color: string) {
  const [r = 0, g = 0, b = 0, a = 1] = color
    .split(/\s+,?/)
    .slice(0, 4)
    .map((c) => parseFloat(c))
    .map((c, i) => (i === 3 ? c : c * 255))
    .map((c) => c.toFixed())

  return `rgba(${r},${g},${b},${a})`
}
