<script lang="ts">
  import { type SvelteComponent, createEventDispatcher, onMount } from "svelte"
  import { Queries, type Schema, gql } from "@api"
  import { analytics } from "@lib/analytics"
  import { Modal, StyleSheet, Tooltips, setIconBase } from "@lib/elements"
  import "@lib/error-tracker"
  import { Router } from "@lib/router"
  import { requestCameraPermissions } from "@lib/utils"
  import { setClientToken } from "@lib/webar"
  import { billingInit } from "@pages/billing"
  import { onDemoEvent } from "@pages/demo"
  import Fonts from "./fonts.css?url"
  import * as pages from "./pages"
  import { setAppContext, setLayoutContext } from "./pages"
  import { type Product, type Sku, appliedProducts, cart } from "./pages/best-match/stores"
  // TODO: move a level up?
  import imageSource from "./pages/best-match/stores/image-source"
  import config from "./token"

  /** The app token */
  export let token: string = import.meta.env.VITE_TINT_VTO_TOKEN
  /** The app SDK token */
  export let sdkToken: string = import.meta.env.VITE_SDK_TOKEN
  /** Specifies if "Terms of use" step should be skipped */
  export let skipTerms = false
  /** Specifies the "Live camera / Upload Photo" step should be skipped */
  export let skipSource = false

  // force cast to boolean since the component may be used as a custom element
  // which props are html attributes and strings by nature
  if (typeof skipTerms !== "boolean") skipTerms = true
  if (typeof skipSource !== "boolean") skipSource = true

  export const open = () => modal.open()
  export const close = () => modal.close()

  let modal: SvelteComponent
  let wcam: { stop(): void }

  export const useWebcam = () =>
    requestCameraPermissions().then((granted) => {
      analytics.camera_permission(granted)
      if (!granted) return null

      $imageSource = { type: "webcam" }

      return (wcam = { stop: () => ($imageSource = null) })
    })

  export const usePhoto = (photo: Blob) => {
    $imageSource = { type: "photo", data: photo }
  }
  export const applyProduct = (...products: Product[]) => appliedProducts.replace(...products)
  export const applyProductBySku = async (...skuIds: Sku["id"][]) => {
    const products = await gql<Pick<Schema.Query, "products">>(Queries.GetProducts, {
      skus: skuIds,
    }).then(({ products }) => products.items)

    if (products.length < skuIds.length)
      for (const skuId of skuIds)
        if (!products.find((p) => p.skus.find((s) => s.id === skuId)))
          console.warn(`The products with SKU ID "${skuId}" is not found or does not exist`)

    applyProduct(...products)
  }
  export const applyProductByCustomerSku = async (...customerSkuIds: Sku["id"][]) => {
    const products = await gql<Pick<Schema.Query, "products">>(Queries.GetProducts, {
      customerSkus: customerSkuIds,
    }).then(({ products }) => products.items)

    if (products.length < customerSkuIds.length)
      for (const skuId of customerSkuIds)
        if (!products.find((p) => p.skus.find((s) => s.id === skuId)))
          console.warn(`The products with SKU ID "${skuId}" is not found or does not exist`)

    applyProduct(...products)
  }

  const dispatch = createEventDispatcher<{
    addToCart: Product[]
    removeFromCart: Product[]
    redirectToCart: void
    demo: void

    applyProduct: Product[]
    removeAppliedProduct: Product[]
    replaceAppliedProduct: Product[]
  }>()

  cart.on("add", (products) => dispatch("addToCart", products as Product[]))
  cart.on("remove", (products) => dispatch("removeFromCart", products as Product[]))
  cart.on("redirect", () => dispatch("redirectToCart"))

  appliedProducts.on("add", (products) => dispatch("applyProduct", products as Product[]))
  appliedProducts.on("remove", (products) =>
    dispatch("removeAppliedProduct", products as Product[])
  )
  appliedProducts.on("replace", (products) =>
    dispatch("replaceAppliedProduct", products as Product[])
  )

  const { brand, entry, routes, customerId } = config(token, {
    terms: !skipTerms,
    imageSourceSelection: !skipSource,
  })

  setClientToken(sdkToken)
  setAppContext({ brand })
  setLayoutContext({ routes: pages.navigation(routes), close, demo: routes.bookADemo })
  // if (brand) setIconBase(`/${brand}/icons/`)

  // page preloading
  let route: { pathname: string } | undefined
  const flow = pages.defaultNavigationFlow(routes, entry)
  $: {
    const next = route ? flow.next(route.pathname) : flow.initial()
    next && flow.preload(next)
  }

  onMount(() => {
    onDemoEvent("pickADate", () => dispatch("demo"))

    if (customerId) billingInit(customerId)
  })
</script>

<StyleSheet href={Fonts} />
{#if brand}
  <StyleSheet href={`/${brand}/fonts.css`} />
  <StyleSheet href={`/${brand}/theme.css`} />
{/if}

<Modal
  class="tint-widget"
  bind:this={modal}
  on:close={() => wcam?.stop()}
  on:open={() => analytics.app_opened()}
  on:close={() => analytics.close_app()}
  on:open
  on:close
>
  <Router
    routes={pages.default(routes)}
    initialPath={entry}
    on:change={({ detail }) => (route = detail)}
  >
    <svelte:element this="slot" name="overlay" />
  </Router>
</Modal>

<Tooltips />

<style lang="postcss" global>
  @import "./global.css";
</style>
