import mitt, { type Handler } from "mitt"
import { writable } from "svelte/store"
import type { Product } from "./types"

type ProductId = Product["id"]
type CartEvent = "add" | "remove" | "redirect"

export const create = () => {
  const { subscribe, update } = writable<ProductId[]>([])
  const emitter = mitt<Record<CartEvent, unknown>>()

  const add = (...products: { id: ProductId }[]) => {
    const toInclude = products.map((p) => p.id)

    update((products) => [...products, ...toInclude])
    emitter.emit("add", products)
  }

  const remove = (...products: { id: ProductId }[]) => {
    const toExclude = new Set(products.map((p) => p.id))

    update((products) => products.filter((p) => !toExclude.has(p)))
    emitter.emit("remove", products)
  }

  const redirect = () => {
    emitter.emit("redirect")
  }

  const on = (event: CartEvent, handler: Handler<unknown> = () => null) => {
    emitter.on(event, handler)
    return () => {
      emitter.off(event, handler)
    }
  }

  return {
    subscribe,

    add,
    remove,
    redirect,
    on,
  }
}
