/** Size-optimized versions of several lodash functions  */

export const debounce = (func: Function, delay = 200) => {
  let timeout: ReturnType<typeof setTimeout> | undefined
  const cancel = () => clearTimeout(timeout)

  const debounced = function (this: any, ...args: any[]) {
    cancel()

    timeout = setTimeout(() => {
      timeout = undefined
      func.apply(this, args)
    }, delay)
  }

  debounced.cancel = cancel

  return debounced
}

let id = 0
export const uniqId = () => `${++id}`

export const random = (min = 0, max = 1) => min + Math.random() * (max - min)

export const uniqBy = <T, K>(arr: T[], iteratee: (item: T) => K): T[] => {
  const seen = new Set<K>()
  const result: T[] = []

  for (const item of arr) {
    const key = iteratee(item)
    if (!seen.has(key)) seen.add(key), result.push(item)
  }

  return result
}
