import mitt, { type Handler } from "mitt"
import { undoable } from "@macfja/svelte-undoable"
import { billingTrackProduct } from "@pages/billing"
import type { Product } from "./types"

export type { Product }

export type Settings = {
  /**
   * Defines if multiple product application is allowed
   * @default true
   */
  multiple?: boolean
}

export type Store = ReturnType<typeof create>

export type AppliedProductsEvent = "add" | "replace" | "remove" | "undo" | "redo"

/**
 * Defines if two products are equal it terms of their applicability.
 *
 * E.g a gloss and a lipstick should be considered as equal, since they booth affect lips.
 * On the other hand, mascara and foundation affects different facial areas and not equal.
 */
const isEqual = (x: Product, y: Product) => {
  const regionX = x.skus[0]?.vto.region
  const regionY = y.skus[0]?.vto.region
  return regionX === regionY
}

export const create = ({ multiple = true }: Settings = {}) => {
  const { subscribe, set, update, undo, redo, canUndo, canRedo, reset } = undoable<Product[]>([])
  const emitter = mitt<Record<AppliedProductsEvent, unknown>>()

  const add = (...products: Product[]) => {
    update(($products) => {
      products.forEach(async ({ id }) => await billingTrackProduct(id))
      if (!multiple || products.some(({ skus }) => Boolean(skus[0]?.vto?.skincare)))
        return products.slice(0, 1)

      const toExclude = new Set(
        $products.filter((prev) => products.find((next) => isEqual(prev, next)))
      )

      return $products.filter((p) => !toExclude.has(p)).concat(products)
    })

    emitter.emit("add", products)
  }

  const replace = (...products: Product[]) => {
    set(products)
    products.forEach(async ({ id }) => await billingTrackProduct(id))

    emitter.emit("replace", products)
  }

  const remove = (...products: Pick<Product, "id">[]) => {
    update(($products) => {
      const toExclude = new Set(products.map((p) => p.id))

      return $products.filter((p) => !toExclude.has(p.id))
    })

    emitter.emit("remove", products)
  }

  const clear = () => reset(true)
  const on = (event: AppliedProductsEvent, handler: Handler<unknown> = () => null) => {
    emitter.on(event, handler)
    return () => {
      emitter.off(event, handler)
    }
  }

  return {
    subscribe,

    add,
    replace,
    remove,
    clear,

    undo,
    redo,
    canUndo,
    canRedo,

    on,
  }
}
