import { Webcam as BnbWebcam, Image, utils } from "@banuba/webar"
import { debounce } from "@lib/utils/lodash"
import * as Logger from "./logger"

class Webcam extends BnbWebcam {
  private readonly _debouncedStop = debounce(function (this: Webcam) {
    Logger.debug("Webcam.stop()")
    this.stop()
  }, 250)

  cancelStop() {
    this._debouncedStop.cancel()
    Logger.debug("Webcam.scheduleStop() canceled")
  }

  scheduleStop() {
    Logger.debug("Webcam.scheduleStop()")
    this._debouncedStop()
  }

  async takePhoto() {
    const video = await utils.createVideoElement(await this["_stream"])
    const canvas = document.createElement("canvas")
    canvas.width = video.videoWidth
    canvas.height = video.videoHeight
    const ctx = canvas.getContext("2d")!

    ctx.drawImage(video, 0, 0)
    ;(video.src = ""), (video.srcObject = null)

    const blob = await new Promise<Blob>((resolve, reject) =>
      canvas.toBlob((blob) =>
        blob ? resolve(blob) : reject(new Error("Unexpected error: Unable to take Webcam photo"))
      )
    )

    return blob
  }
}

const webcam = new Webcam({
  width: { ideal: 1920 },
  height: { ideal: 1080 },
  facingMode: { ideal: "user" },
  // @ts-expect-error
  resizeMode: "none",
})

const images = new WeakMap<Blob, Image>()

export const get = ((key: "webcam" | Blob) => {
  if (key === "webcam") return webcam

  if (!images.has(key)) images.set(key, new Image(key))

  return images.get(key)!
}) as {
  (key: "webcam"): Webcam
  (key: Blob): Image
}
