/**
 * Borrowed idea from https://github.com/romkor/svelte-portal/blob/master/src/Portal.svelte
 *
 * Unfortunately, the referenced package can not be used directly in the app,
 * since it uses `instanceof HTMLElement` check (https://github.com/romkor/svelte-portal/blob/master/src/Portal.svelte#L25).
 * The check does not play well with Realms (see @lib/widget for detail on the Realm).
 * If the action runs inside a Realm and it tries to teleport an `element` to a `target` located outside of the Realm,
 * e.g `target` is located at the main Document element,
 * the `instanceof` check will fail since built-ins like HTMLElement are different between the Real and outside Realm/Document.
 * In simple words, Realm.HTMLElement !== Document.HTMLElement.
 * Thus, we have to write the action ourselves, omitting the `instanceof` check.
 */

/**
 * Usage: <div use:portal={document.body}>
 *
 * @param {HTMLElement} element an HTML Element
 * @param {HTMLElement} target DOM Element
 */
export const portal = (element: HTMLElement, target: HTMLElement | ShadowRoot) => {
  target.appendChild(element)

  return {
    update: (target: HTMLElement | ShadowRoot) => target.appendChild(element),
    destroy: () => element.parentNode && element.parentNode.removeChild(element),
  }
}
