import type { PathOrLocation } from "./history"

export const normalize = (path = "") => {
  if (path.endsWith("/")) path = path.slice(0, -1)
  if (!path.startsWith("/")) path = "/" + path
  return path
}

export const join = (...paths: string[]) => paths.map(normalize).join("").replace("//", "/")

export const resolve = (...paths: string[]) => {
  const chunks = []

  for (let path of paths) {
    path = path.endsWith("/") ? path.slice(0, -1) : path

    for (const part of path.split("/")) {
      if (part === "..") chunks.pop() // omit last
      if (part.startsWith(".")) continue // do nothing
      if (part === "") chunks.length = 0 // reset
      else chunks.push(part)
    }
  }

  return normalize(chunks.join("/"))
}

export const interopDefault = <T>(obj: T): T extends { default: infer D } ? D : T =>
  obj && ("default" in obj ? (obj as any).default : obj)

export const match = (
  pattern: PathOrLocation,

  path: string,

  options = { exact: false }
): boolean => {
  const currentPath = typeof pattern === "string" ? pattern : pattern.pathname

  return options.exact ? currentPath === path : currentPath.includes(path)
}
