export type Query = Record<string, string | number | boolean>

export const create = (base: string, headers?: HeadersInit) => {
  const doFetch = (request: RequestInfo | URL) => {
    if (!(request instanceof Request)) request = new Request(request)

    if (headers)
      for (const [key, value] of Object.entries(headers)) request.headers.append(key, value)

    return fetch(request).then((r) =>
      r.headers.get("content-type")?.includes("application/json") ? r.json() : r.blob()
    )
  }

  const get = async <T>(path: string, query: Query = {}): Promise<T> => {
    const url = new URL(path, base)

    for (const [name, value] of Object.entries(query)) url.searchParams.append(name, String(value))

    return doFetch(url)
  }

  const post = async <T>(
    path: string,
    data?: string | FormData | Record<string, any>
  ): Promise<T> => {
    const url = new URL(path, base)
    const headers: HeadersInit = {}

    if (data instanceof FormData) headers["content-type"] = "multipart/form-data"
    else if (typeof data === "object")
      (headers["content-type"] = "application/json"), (data = JSON.stringify(data))

    return doFetch(
      new Request(url, {
        headers,
        method: "POST",
        body: data,
      })
    )
  }

  return { get, post }
}
