import type { Rect } from "@floating-ui/core"

/**
 * The default [platform](https://floating-ui.com/docs/platform) implementation of @floating-ui/dom
 * highly relies on `instanceof HTMLElement` checks, which do not play well with Realms (see @lib/widget for detail on the Realm).
 * To workaround the issue, we have to implement the `platform` binding ourselves.
 *
 * The code below is mainly inspired by https://github.com/floating-ui/floating-ui/blob/master/packages/dom/src/platform.ts
 * There are lots of admissions and simplifications to the original methods.
 */

export const getElementRects = ({
  reference,
  floating,
}: {
  reference: HTMLElement
  floating: HTMLElement
}) => ({
  // https://github.com/floating-ui/floating-ui/blob/master/packages/dom/src/platform.ts#L18
  reference: getRectRelativeToOffsetParent(reference, getOffsetParent(floating)!),
  floating: { x: 0, y: 0, ...getDimensions(floating) } as Rect,
})

export const getDimensions = (element: HTMLElement) => ({
  width: element.offsetWidth,
  height: element.offsetHeight,
})

export const getClippingRect = ({ element }: { element: HTMLElement }) => {
  /**
   * Highly simplified version of https://github.com/floating-ui/floating-ui/blob/master/packages/dom/src/utils/getClippingRect.ts
   * since we know the app renders all the floating elements to the app's root which mush have `position: relative`
   */

  return getOffsetParent(element)!.getBoundingClientRect() as Rect
}

/** Reverse operation to {@link getRectRelativeToOffsetParent} */
export const convertOffsetParentRelativeRectToViewportRelativeRect = ({
  rect,
  offsetParent,
}: {
  rect: Rect
  offsetParent: HTMLElement
}) => {
  /**
   * Simplified version of https://github.com/floating-ui/floating-ui/blob/master/packages/dom/src/utils/convertOffsetParentRelativeRectToViewportRelativeRect.ts
   * which does not take into account root element scroll
   */

  const parentRect = offsetParent.getBoundingClientRect()

  return {
    ...rect,
    x: rect.x + (parentRect.left + offsetParent.clientLeft),
    y: rect.y + (parentRect.top + offsetParent.clientTop),
  } as Rect
}

export const getOffsetParent = (element: HTMLElement) => {
  let offsetParent = element.offsetParent

  if (!offsetParent)
    while ((offsetParent = element.parentElement))
      if (/(relative|absolute|fixed)/.test(getComputedStyle(offsetParent).position)) break

  return offsetParent as null | HTMLElement
}

export const getDocumentElement = (element: HTMLElement) => element.ownerDocument

export const getClientRects = (/* element: HTMLElement */) => {
  /**
   * The method is not currently used by any of @floating-ui/core middlewares the app consumes.
   * Since the method is not used hence untested, it's better to throw an error for now,
   * so future developers can notice it and test the method.
   */

  throw new Error(`The "getClientRects" is not implemented`)

  /** Guessing that the code commented below should works just fine. */

  // return [...element.getClientRects()]
}

export const isElement = (obj: any): obj is HTMLElement => {
  /**
   * Realm-friendly check of obj being an instance of HTMLElement w/o usage of `instanceof` keyword
   * Inspired by https://stackoverflow.com/a/384380
   */
  return obj && obj.nodeType === Node.ELEMENT_NODE && obj.style && obj.ownerDocument
}

export const isRTL = () => {
  /** Should be re-implemented once the app uses RTL layout (e.g arabic language) */

  return false
}

const getRectRelativeToOffsetParent = (element: HTMLElement, parent: HTMLElement) => {
  /**
   * Simplified version of https://github.com/floating-ui/floating-ui/blob/master/packages/dom/src/utils/getRectRelativeToOffsetParent.ts
   * which does not take into account root element scroll
   */

  const elementRect = element.getBoundingClientRect()
  const parentRect = parent.getBoundingClientRect()

  return {
    x: elementRect.left - (parentRect.left + parent.clientLeft),
    y: elementRect.top - (parentRect.top + parent.clientTop),
    width: elementRect.width,
    height: elementRect.height,
  } as Rect
}
