<script lang="ts">
  import { createEventDispatcher } from "svelte"
  import { routerPath } from "@lib/router"
  import { isMobileDevice } from "@lib/utils/is-mobile-device"
  import { getContext } from "@pages/_layout/context"
  import IconButton from "../IconButton"

  export let isOpen = false

  export const open = () => (isOpen = true)
  export const close = () => (isOpen = false)

  const dispatch = createEventDispatcher<{ open: void; close: void }>()

  const ctx = getContext()
  $: showClose =
    (!isMobileDevice() || !ctx.routes.map(({ path }) => path).includes($routerPath.current)) &&
    !ctx.demo

  $: dispatch(isOpen ? "open" : "close")
</script>

<dialog {...$$restProps} data-open={isOpen}>
  <slot />
  {#if showClose}
    <IconButton class="close-button" icon="close" variant="overlay" on:click={close} />
  {/if}
</dialog>

<style lang="postcss">
  dialog {
    @apply fixed top-0 left-0 z-[9999999] flex h-full w-full appearance-none items-center justify-center bg-black/40 p-0 xl:p-10;

    :global(button.close-button) {
      @apply absolute top-3 right-3 z-20 md:top-2 md:right-2;
    }

    &:not([data-open="true"]) {
      @apply hidden;
    }

    & > :global(:first-child) {
      @apply overflow-hidden bg-white;
    }
  }
</style>
