import type { Feature, Product } from "../../token-generator/types"
import type { Settings as RotesSettings } from "./pages"

/** Decoded token */
type Config = {
  features: Feature[]
  products: Product[]
  brand?: string
  entry?: string
  customerId?: false
}

const RouteSettingByConfigFeature: Partial<Record<Feature, keyof RotesSettings>> = {
  camera_analysis: "faceAnalysis",
  makeup_virtual_try_on: "bestMatch",
  makeup_transfer: "makeupTransfer",
  gift_selection: "giftSelection",
  beauty_ai_advisor: "beautyAdvisor",
  book_a_demo: "bookADemo",
  skin_care: "skinCare",
}

type RouteSettings = Record<keyof RotesSettings, boolean>

export default (token: string, overrides?: Partial<RouteSettings>) => {
  const { brand, entry = "best-match", features, customerId } = parse(token)

  const routes = {} as RouteSettings

  for (const feature of features) {
    const setting = RouteSettingByConfigFeature[feature]
    if (setting) routes[setting] = true
  }

  Object.assign(routes, overrides)

  return {
    brand,
    entry,
    routes,
    customerId,
  }
}

export const parse = (token: string) => {
  try {
    return JSON.parse(window.atob(token)) as Config
  } catch {
    console.error("Incorrect or malformed token")

    return {
      features: [],
      products: [],
    }
  }
}
