import { derived, get, writable } from "svelte/store"
import type { AnalysisProgress, AnalysisResult } from "@lib/sca"

export type { AnalysisResult }

export const percent = writable<AnalysisProgress["percent"]>(0)
export const step = writable<AnalysisProgress["step"]>(0)
export const completed = derived(percent, ($percent) => $percent === 100)

export const result = writable<AnalysisResult | null>(null)

export const isEmpty = () => !get(completed)

export const clear = () => {
  result.set(null)
  step.set(0)
  percent.set(0)
}
