import type { Effect, Input } from "@banuba/webar"
import { isMultiThreaded } from "./env"

export type State = {
  effect: Effect | null
  source: Input | null
  lastSource: Input | null
}

const createState = (): State => {
  return {
    effect: null,
    source: null,
    lastSource: null,
  }
}

export const Analysis = createState()
export const Beauty = isMultiThreaded ? createState() : Analysis
