import { create as createHttpClient } from "./http"

type GraphQLQuery = string
type GraphQLQueryVariables = Record<string, any>

export const create = (base: string, headers?: HeadersInit) => {
  const http = createHttpClient(base, headers)

  return <T>(query: GraphQLQuery, variables: GraphQLQueryVariables = {}) =>
    http.post<{ data: T; errors: any }>("", { query, variables }).then(({ data, errors }) => {
      if (!errors) return data
      else throw errors // TODO: implement better error handling
    })
}
