<script lang="ts">
  import { writable } from "svelte/store"
  import { fade } from "svelte/transition"
  import { cart } from "@pages/best-match/stores"
  import IconButton from "../IconButton"
  import { clickOutside, cx, getSvelteRoot } from "../utils"
  import { setOpen } from "./context"

  const root = getSvelteRoot()

  let hover = false
  let open = false

  const ctx = setOpen(writable(false))

  $: $ctx = open || hover
  $: classes = cx("tint-navbar", $$restProps.class)
  $: menuClasses = cx("menu", $cart.length > 0 && "cart")
</script>

<div class:open>
  <IconButton
    class={menuClasses}
    icon="menu"
    variant="overlay"
    size="lg"
    on:click={() => (open = true)}
  />
  <nav
    class:open
    class:hover
    class={classes}
    use:clickOutside={root}
    on:click_outside={() => (open = false)}
    on:mouseenter={() => (hover = true)}
    on:mouseleave={() => (hover = false)}
  >
    <i class="logo"><slot name="logo" /></i>
    <slot close={() => (open = false)} />
    <p in:fade out:fade>Powered by <b>Banuba</b></p>
  </nav>
</div>

<style lang="postcss">
  div {
    @apply relative z-50 h-full transform-gpu;
    @apply w-0 lg:w-16;

    &::before {
      @apply transition-colors content-[''];
      @apply lg:content-none;
    }

    &.open::before {
      @apply absolute left-0 top-0 h-full w-screen bg-black/30;
    }
  }

  nav {
    @apply flex flex-col;

    & > :global(*) {
      @apply shrink-0;
    }

    &:hover p,
    &.open p {
      @apply opacity-100;
    }
  }

  :global(.menu) {
    @apply absolute top-3 left-3 z-40 md:left-4;
    @apply lg:hidden;
  }

  :global(.menu.cart)::before {
    @apply absolute top-0 right-0 block h-3 w-3 rounded-full bg-[#ff2d55] content-[""];
  }

  div > :global(:last-child) {
    @apply relative z-50 h-full w-[17.5rem] overflow-hidden bg-[#16161a] px-3 py-4 text-white transition-[width,transform] duration-300;
    @apply lg:w-full lg:hover:w-[17.5rem];

    &:not(.open) {
      @apply -translate-x-full lg:translate-x-0;
    }
  }

  .logo {
    @apply mb-2 flex h-[3.75rem] items-center justify-start;
  }

  p {
    @apply whitespace-nowrap text-center text-xs text-opacity-60 opacity-0;

    & > b {
      @apply text-opacity-100;
    }
  }
