import { derived, writable } from "svelte/store"

type Brand = {
  name: string
  slug: string
  active: boolean
}

const brandFilters = writable<
  {
    name: Brand["name"]
    slug: Brand["slug"]
  }[]
>([])

export const data = { brandFilters }

type Predicate = {
  brand: Brand["name"]
}

export const create = () => {
  const predicate = writable<Predicate>({ brand: "all" })
  const brands = derived([brandFilters, predicate], ([$filters, $predicate]) =>
    $filters.map((filter) => ({
      ...filter,
      active: $predicate?.brand === filter.slug,
    }))
  )

  const { subscribe } = derived([brands, predicate], ([$brands, $predicate]) => ({
    brands: $brands,
    predicate: $predicate,
  }))

  const filter = (by: Predicate) => predicate.set(by)
  const filterByBrand = (brand: { slug: Brand["slug"] }) => filter({ brand: brand.slug })

  return {
    subscribe,

    filter,
    filterByBrand,
  }
}
