import { type Client, create } from "./client"

declare global {
  interface Window {
    [DATA_LAYER]: DataLayer
  }
}

interface DataLayer {
  push(event: { event: string; [key: string]: any }): void
}

const DATA_LAYER = "dataLayer"

const isEnabled = import.meta.env.VITE_GTM_ID

let gtm: Client | null = null

if (isEnabled)
  gtm = create(() =>
    new Promise<DataLayer>((resolve, reject) => {
      const script = document.createElement("script")
      script.defer = true
      script.onload = () => resolve(script.ownerDocument?.defaultView?.[DATA_LAYER]!)
      script.onerror = reject
      script.src = `https://www.googletagmanager.com/gtm.js?id=${
        import.meta.env.VITE_GTM_ID
      }&l=${encodeURIComponent(DATA_LAYER)}`

      // window.frameElement.parentNode - widget root if the app consumed as isolated widget
      const head = window.frameElement?.parentNode || document.head

      head.appendChild(script)
    }).then((dataLayer) => {
      dataLayer.push({ event: "gtm.js", "gtm.start": new Date().getTime() })

      const log: Client["log"] = (eventName, eventProps = {}) => {
        if (typeof eventProps !== "object" || eventProps === null) eventProps = { eventProps }
        dataLayer.push({ event: eventName, ...eventProps })
      }

      return { log }
    })
  )

export default gtm
