import mitt, { type Handler } from "mitt"

type DemoEvent = "pickADate" | "showAd"

const demoEmitter = mitt<Record<DemoEvent, unknown>>()

type FlowType = "Products" | "Cart add" | "Look"

type AdDetail = {
  flow?: FlowType
}

export const showAd = (detail?: AdDetail) => demoEmitter.emit("showAd", detail)

export const pickADate = () => demoEmitter.emit("pickADate")

export const onDemoEvent = (event: DemoEvent, handler: Handler<unknown>) =>
  demoEmitter.on(event, handler)
