<!-- Base element for Popup, Tooltips, etc -->
<script lang="ts" context="module">
  import type { Placement as FloatingUIPlacement } from "@floating-ui/core"

  type Placement =
    | FloatingUIPlacement
    | "top-left-corner"
    | "top-right-corner"
    | "bottom-right-corner"
    | "bottom-left-corner"

  export type { Placement }
</script>

<script lang="ts">
  import { type SvelteComponent, createEventDispatcher, onMount, tick } from "svelte"
  import { sineInOut as easing } from "svelte/easing"
  import { fade } from "svelte/transition"
  import {
    computePosition,
    offset as offsetMiddleware,
    shift as shiftMiddleware,
    size as sizeMiddleware,
  } from "@floating-ui/core"
  import { clickOutside, getSvelteRoot, portal } from "../utils"
  import * as platform from "./platform"

  export let anchor: HTMLElement | SvelteComponent | undefined = undefined
  export let placement: Placement = "bottom"
  export let offset: [mainAxis: number, crossAxis: number] = [0, 0]

  let trigger: HTMLElement | null = null
  let popup: HTMLElement
  const root = getSvelteRoot()
  const dispatch = createEventDispatcher<{ open: void; close: void }>()

  export const open = (anchor: HTMLElement) => (trigger = anchor)
  export const close = () => (trigger = null)

  $: dispatch(trigger ? "open" : "close")

  $: tick() // wait for svelte to popuate the "popup" element refernce
    .then(
      () =>
        trigger &&
        computePosition(trigger, popup, {
          platform,
          strategy: "absolute",
          placement: (placement.endsWith("-corner")
            ? placement.replace("-corner", "").replace("left", "start").replace("right", "end")
            : placement) as FloatingUIPlacement,
          middleware: [
            offsetMiddleware(({ rects }) => {
              let [mainAxis, crossAxis] = offset

              if (placement.endsWith("-corner"))
                if (/(left|right)/.test(placement))
                  crossAxis += (/left/.test(placement) ? -1 : 1) * rects.floating.width

              return { mainAxis, crossAxis }
            }),
            sizeMiddleware({
              // TODO: somehow retrieve the boundary, i.e. the Widget's current page
              // boundary: ? ,
              apply: ({ availableHeight }) => {
                Object.assign(popup.style, { maxHeight: `${availableHeight}px` })
              },
            }),
            shiftMiddleware({ padding: 12 }),
          ],
        }).then(({ x, y }) => Object.assign(popup.style, { left: `${x}px`, top: `${y}px` }))
    )

  onMount(() => {
    const p = (anchor ? Promise.resolve() : tick()) // wait for svelte to popuate the "anchor" element/component refernce
      .then(() => {
        if (!anchor) {
          // looks like anchor was not provided
          // and the Floating is going to be controlled externally via open / close methods
          return () => {}
        }

        const isHTMLElement = anchor instanceof HTMLElement

        const doOpen = ({ currentTarget }: MouseEvent) => open(currentTarget as HTMLElement)

        return isHTMLElement
          ? (anchor.addEventListener("click", doOpen),
            () => anchor!.removeEventListener("click", doOpen))
          : (anchor as SvelteComponent).$on("click", doOpen)
      })

    return () => p.then((cleanup) => cleanup())
  })
</script>

{#if trigger}
  <div
    class={$$restProps.class}
    transition:fade|local={{ duration: 200, easing }}
    use:portal={root}
    use:clickOutside={root}
    bind:this={popup}
    on:click_outside={close}
  >
    <slot {close} />
  </div>
{/if}

<style lang="postcss">
  div {
    @apply absolute top-0 left-0 z-[99999999] flex flex-col;

    & > :global(*) {
      @apply min-h-0;
    }
  }
</style>
