<script context="module" lang="ts">
  import { get, writable } from "svelte/store"
  import type { Location, PathOrLocation } from "./history"
  import type { RouteConfig as Config } from "./resolver"

  type DefaultContext = { location: Location }
  type RouteConfig<T extends Record<string, any> = DefaultContext> = Config<
    T & DefaultContext,
    PathOrLocation
  >

  type RouterPath = {
    current: Paths
    previous: Paths
  }

  export enum Paths {
    BestMatch = "/best-match",
    SkinCare = "/skin-care",
    GiftSelection = "/gift-selection",
    MakeupTransfer = "/makeup-transfer",
    BeautyAdvisor = "/beauty-advisor",
    BookADemo = "/book-a-demo",
    Terms = "/terms",
    ImageSource = "/image-source",
    FaceAnalysis = "/face-analysis",
    Empty = "",
  }

  export const routerPath = writable<RouterPath>({ current: Paths.Empty, previous: Paths.Empty })
  export const dataPath = writable<Paths>(Paths.Empty)
  export const loading = writable<boolean>(false)
  export const isDataPathChanged = () =>
    [Paths.BestMatch, Paths.GiftSelection, Paths.SkinCare].includes(get(routerPath).current) &&
    get(routerPath).current !== get(dataPath)

  export const updateDataPath = () => dataPath.set(get(routerPath).current)

  export type { DefaultContext, RouteConfig, RouterPath }
</script>

<script lang="ts">
  import { createEventDispatcher, tick } from "svelte"
  import ComponentTree from "./ComponentTree.svelte"
  import { setRouter } from "./context"
  import { createMemoryHistory } from "./history"
  import type { ResolvedRoute } from "./resolver"
  import { createResolve } from "./resolver"
  import { normalize } from "./utils"

  type T = $$Generic<Record<String, any>>

  export let routes: RouteConfig<T>[]
  export let initialPath = ""
  export let context: T extends DefaultContext ? never : T = undefined as never

  const ctx = context
  const resolve = createResolve(routes)
  const history = createMemoryHistory(normalize(initialPath))
  const dispatch = createEventDispatcher<{ change: ResolvedRoute | undefined }>()

  let location: Location
  let route: ResolvedRoute | undefined
  let pending = Promise.resolve()

  $: location = $history.location
  $: components = route ? route.components : []
  $: pending = pending
    .then(() => tick())
    .then(() => resolve(location.pathname, { ...ctx, location }))
    .then((resolved) => {
      if (!resolved) return console.warn(`A route for path "${location.pathname}" is not found.`)

      if ("redirect" in resolved) return history.replace(resolved.redirect)

      route = resolved
    })
  $: {
    dispatch("change", route)
    const currentPath = $routerPath.current
    $routerPath = { previous: currentPath, current: route?.pathname || Paths.Empty }
  }

  setRouter(history)
</script>

<ComponentTree {components}>
  <slot />
</ComponentTree>
