import { type Readable, derived, get, writable } from "svelte/store"
import type { Schema } from "@api"
import type { Store as AppliedLooksStore } from "./applied-products"
import type { Look } from "./types"

type Category = {
  type: string
  name: string
  active?: boolean
}

type LooksListLook = Look & { active: boolean }

const types = writable<string[]>([])
const items = writable<Schema.Look[]>([])

export const data = { types, items }

const CategoryNameByType = {
  POPULAR: "Popular",
}

export const create = (appliedProducts: AppliedLooksStore) => {
  const activeCategory = writable<Category["type"]>()
  const categories = derived([types, activeCategory], ([$types, $active]) => {
    return $types.map((type) => ({
      type,
      name: CategoryNameByType[type as keyof typeof CategoryNameByType],
      active: type === $active,
    }))
  })

  const appliedLook = derived([items, appliedProducts], ([$items, $appliedProducts]) => {
    const applied = new Set($appliedProducts.map((p) => p.id))
    return $items.find((l) => l.products.products.every((p) => applied.has(p.id)))?.id
  })
  const looks: Readable<LooksListLook[]> = derived(
    [items, appliedLook],
    ([$items, $appliedLook]) => {
      return $items.map((look) => ({
        ...look,
        active: look.id === $appliedLook,
      }))
    }
  )

  const { subscribe } = derived(
    [categories, looks, appliedLook],
    ([$categories, $items, $appliedLook]) => ({
      categories: $categories,
      items: $items,
      appliedLook: $items.find(({ id }) => id === $appliedLook),
    })
  )

  const changeCategory = ({ type }: Category) => activeCategory.set(type)
  const select = (look: LooksListLook) => appliedProducts.replace(...look.products.products)
  const clear = (look: LooksListLook) => appliedProducts.remove(...look.products.products)

  const unsubscribe = types.subscribe(($types) => {
    if (!$types.length) return

    const active = get(activeCategory)
    if (!active) activeCategory.set($types[0])

    setTimeout(unsubscribe)
  })

  return {
    subscribe,

    changeCategory,

    select,
    clear,
  }
}
