import { readable } from "svelte/store"
import { getSvelteRoot } from "./get-root"

export const media = (...args: Parameters<typeof matchMedia>) => {
  const root = getSvelteRoot()
  // ownerDocument.defaultView - cross Realm way to access window
  const window = root.ownerDocument.defaultView!

  const mq = window.matchMedia(...args)

  return readable<boolean>(mq.matches, (set) => {
    mq.onchange = ({ matches }) => set(matches)

    return () => (mq.onchange = null)
  })
}
