import type { Event, Props } from "./client"
import gtm from "./gtm"
import mixpanel from "./mixpanel"

const log = async (...args: [event: Event, props?: Props]) => {
  if (gtm) gtm.log(...args)
  if (mixpanel) mixpanel.log(...args)

  console.debug("[analytics] log(", ...args, ")")
}

export const analytics = new Proxy(
  {},
  { get: (_, prop: Event) => (data?: Props) => log(prop, data ?? {}) }
) as Record<Event, (data?: Props) => void>
