/// <reference path="./resources.d.ts" />
import { isSimdSupported } from "@banuba/webar"
import data from "@banuba/webar/BanubaSDK.data?url"
import simd from "@banuba/webar/BanubaSDK.simd.wasm?url"
import wasm from "@banuba/webar/BanubaSDK.wasm?url"
import eyes from "@banuba/webar/eyes.zip?url"
import frx from "@banuba/webar/face_tracker.zip?url&exclude[]=flow/face_detector_online.tflite&exclude[]=flow/face_detector_online.tflite.bbin&exclude[]=flow/face_detector_offline.tflite&exclude[]=flow/face_detector_offline.tflite.bbin&exclude[]=flow/landmarks_224.tflite.bbin"
import frx_offline from "@banuba/webar/face_tracker.zip?url&include[]=flow/face_detector_offline.tflite&include[]=flow/face_detector_offline.tflite.bbin&include[]=flow/landmarks_224.tflite.bbin"
import frx_online from "@banuba/webar/face_tracker.zip?url&include[]=flow/face_detector_online.tflite&include[]=flow/face_detector_online.tflite.bbin"
import frx_lite from "@banuba/webar/face_tracker_lite.zip?url&exclude[]=flow/face_detector_online.tflite&exclude[]=flow/face_detector_online.tflite.bbin&exclude[]=flow/face_detector_offline.tflite&exclude[]=flow/face_detector_offline.tflite.bbin&exclude[]=flow/landmarks_224.tflite.bbin"
import frx_lite_offline from "@banuba/webar/face_tracker_lite.zip?url&include[]=flow/face_detector_offline.tflite&include[]=flow/face_detector_offline.tflite.bbin&include[]=flow/landmarks_224.tflite.bbin"
import frx_lite_online from "@banuba/webar/face_tracker_lite.zip?url&include[]=flow/face_detector_online.tflite&include[]=flow/face_detector_online.tflite.bbin"
import lips from "@banuba/webar/lips.zip?url"
import face from "@banuba/webar/skin.zip?url&include[]=flow/face_segm.tflite.bbin"
import skin from "@banuba/webar/skin.zip?url&include[]=flow/skin_segm.tflite.bbin"
import { preload as _preload } from "../../utils"
import Analysis from "../effects/empty_effect.zip?url"
import Makeup from "../effects/makeup.zip?url"
import SkinCare from "../effects/skin_care.zip?url"
import Beauty from "../effects/ultima?zip"

type Resource = string | (() => string | Promise<string>)

const _isBeautyEffect = import.meta.env.VITE_BEAUTY_EFFECT === "true"

export const SDK = [async () => ((await isSimdSupported()) ? simd : wasm), data]

export const AnalysisResources = [
  isSafari() ? frx_lite : frx,
  isSafari() ? frx_lite_online : frx_online,
  eyes,
]

export const BeautyResources = (filter?: "online" | "offline") => {
  const all = typeof filter === "undefined"
  const online = all || filter === "online"
  const offline = all || filter === "offline"

  return _isBeautyEffect
    ? [
        isSafari() ? frx_lite : frx,
        online && (isSafari() ? frx_lite_online : frx_online),
        offline && (isSafari() ? frx_lite_offline : frx_offline),
        face,
        offline && skin,
        lips,
      ].filter(Boolean)
    : [
        isSafari() ? frx_lite : frx,
        online && (isSafari() ? frx_lite_online : frx_online),
        offline && (isSafari() ? frx_lite_offline : frx_offline),
        face,
        eyes,
      ].filter(Boolean)
}

export enum EffectType {
  beauty,
  makeup,
  skinCare,
}

export const getEffect = (effect: EffectType) => {
  switch (effect) {
    case EffectType.beauty:
      return Beauty
    case EffectType.makeup:
      return Makeup
    case EffectType.skinCare:
      return SkinCare
  }
}

export const AnalysisEffect = Analysis
export const BeautyEffect = Beauty
export const MakeupEffect = Makeup
export const SkinCareEffect = SkinCare

export const preload = async (...resources: (Resource | Resource[])[]) => {
  await Promise.all(
    resources.flat(2).map((resource) => {
      if (typeof resource === "string") return _preload(resource)

      return Promise.resolve(resource()).then(_preload)
    })
  )
}

function isSafari() {
  return /(iP(hone|od|ad).*)|(Version\/)\d+.*Safari/.test(navigator.userAgent)
}
