import { maxBy, random } from "lodash"
import { derived, get, writable } from "svelte/store"
import { ProductType } from "@api/enums"
import { Paths, routerPath } from "@lib/router"
import { setFaceSmooth } from "@lib/webar/beauty"
import { appliedProducts } from "@pages/best-match/stores"
import type { Problem, ProblemData, QuizData } from "./data"
import { SkinCareRegion, problemsData } from "./data"

export type { QuizData, SkinCareRegion, ProblemData, Problem }

export type Store = ReturnType<typeof create>

const getProblemsByAge = (age: number): Problem[] => {
  const problems: Problem[] = []

  if (age >= 14 && age <= 25) {
    problems.push("acne")
  }

  if (age > 25) {
    problems.push("pores")
  }

  if (age > 30) {
    problems.push("wrinkles")
  }

  return problems
}

const calculateEffectValue = (week: number, max: number, age: number): number => {
  const t = week / (max || 1)
  const t0 = 0.35
  const b = -0.08 * age + 10 // 16-80 years 9.0-4.0
  const maxEffectValue = 3

  return maxEffectValue / (1 + Math.exp(-b * (t - t0)))
}

export const create = () => {
  const problems = writable<Problem[]>([])
  const age = writable<number>(0)
  const problemsVariant = writable<number>(0)
  const recommendationList = writable<Set<ProductType>>(new Set())
  const isQuizDone = writable<boolean>(false)
  const showQuizWizard = writable<boolean>(false)
  const showProblems = writable<boolean>(false)
  const rangeValue = writable<number>(0)
  const enabled = derived(routerPath, ($routerPath) => $routerPath.current === Paths.SkinCare)
  const appliedProductVto = derived(
    appliedProducts,
    ($appliedProducts) =>
      maxBy($appliedProducts, (item) => item?.skus[0]?.vto.skincare?.max)?.skus[0]?.vto
  )

  const setEffect = async (data: number) => {
    if (!get(enabled)) {
      return
    }

    if (get(appliedProductVto)?.skincare) {
      showProblems.set(false)

      const max = get(appliedProductVto)?.skincare?.max || 1
      const value = calculateEffectValue(data, max, get(age))

      if (get(appliedProductVto)?.region === SkinCareRegion.Foundation) {
        await setFaceSmooth(value, 0)
      }
      if (get(appliedProductVto)?.region === SkinCareRegion.Concealer) {
        await setFaceSmooth(0, value)
      }
    } else {
      showProblems.set(true)
      await setFaceSmooth(0, 0)
      rangeValue.set(0)
    }
  }

  rangeValue.subscribe(setEffect)

  appliedProducts.subscribe((products) => {
    if (get(enabled)) {
      showProblems.set(products.length === 0)
      rangeValue.set(0)
    }
  })

  const setQuizData = (data: QuizData) => {
    isQuizDone.set(true)
    age.set(data.age)

    const problemsByAge = getProblemsByAge(data.age)

    const problemsSet = Array.from(new Set([...data.problems, ...problemsByAge])).filter(
      (problem) => problem !== "none"
    )

    problems.set(problemsSet)

    const dataList: ProductType[] = [
      ProductType.FacialMask,
      ProductType.Serum,
      ProductType.Exfolitators,
      ProductType.HydratingCream,
      ProductType.Toner,
    ]

    problemsSet.forEach((problem) => {
      dataList.push(...problemsData[problem].products)
    })

    recommendationList.set(new Set(dataList))

    showProblems.set(true)
  }

  const getProblemData = (problem: Problem): ProblemData => problemsData[problem]

  const getProblemsList = (): Problem[] => Object.keys(problemsData) as Problem[]

  const resetQuiz = () => {
    isQuizDone.set(false)
    problems.set([])
    recommendationList.set(new Set())
    showProblems.set(false)
    problemsVariant.set(random(0, 9))
  }

  const hide = async () => {
    if (get(enabled)) {
      await setFaceSmooth(0, 0)
    }
  }

  const show = async () => {
    if (get(enabled)) {
      await setEffect(get(rangeValue))
    }
  }

  const exports = {
    setQuizData,
    resetQuiz,

    getProblemData,
    getProblemsList,

    problems,
    isQuizDone,
    showQuizWizard,
    showProblems,
    rangeValue,

    hide,
    show,

    enabled,
    recommendationList,
    problemsVariant,
  }

  const { subscribe, set, update } = writable<typeof exports>()

  return {
    subscribe,
    set,
    update,

    ...exports,
  }
}
