let sessionId: string
let customerId: string

const trackedProducts: Set<string> = new Set()

export const billingInit = (id: string) => {
  customerId = id

  fetch(`${import.meta.env.VITE_BILLING_URL}/v1/customer/session?id=${customerId}`, {
    method: "POST",
  })
    .then((res) => res.json())
    .then((data) => (sessionId = data))
}

export const billingTrackProduct = async (itemId: string) => {
  if (!customerId) {
    return
  }

  if (!trackedProducts.has(itemId)) {
    trackedProducts.add(itemId)
    console.debug("[billing] try on product", itemId)

    const hasVto = await fetch(`${import.meta.env.VITE_BILLING_URL}/v1/customer/has-vto`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      method: "POST",
      body: JSON.stringify({
        customerId: parseInt(customerId),
        sessionId,
      }),
    }).then((res) => res.json())

    if (hasVto) {
      await fetch(`${import.meta.env.VITE_BILLING_URL}/v1/customer/consume-vto`, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        method: "POST",
        body: JSON.stringify({
          customerId: parseInt(customerId),
          sessionId,
          itemId: parseInt(itemId),
        }),
      }).then((res) => res.json())
      return true
    } else {
      return false
    }
  }
}
