/// <reference path="./click-outside.d.ts" />

// Adopted from https://svelte.dev/repl/0ace7a508bd843b798ae599940a91783?version=3.16.7
// the `ownerDocument` allows to use the action across Realms
// https://developer.mozilla.org/en-US/docs/Web/API/Node/ownerDocument
export const clickOutside = (node: Node, bounding: Node | ShadowRoot = node.ownerDocument!) => {
  const handleClick = (event: Event) => {
    if (node && !node.contains(event.target as Node) && !event.defaultPrevented)
      node.dispatchEvent(new CustomEvent("click_outside", node as CustomEventInit))
  }

  bounding.addEventListener("click", handleClick, true)

  return {
    destroy: () => bounding.removeEventListener("click", handleClick, true),
  }
}
