import {
  Dom,
  Effect,
  type FrameData,
  Image,
  Module,
  type PixelsFrame,
  type Player,
} from "@banuba/webar"
import PlayerConfig from "./shared/config"
import { crop } from "./shared/crop"
import { isMultiThreaded } from "./shared/env"
import * as Sources from "./shared/image-sources"
import * as Logger from "./shared/logger"
import { resize } from "./shared/resize"
import * as Resources from "./shared/resources"
import { Analysis as State } from "./shared/state"

const FRAME_DATA_EVENT = "framedata"

let _player: Promise<Player>
let _modules: Module[]
let _effect: Effect

export const getPlayer = async () => {
  if (!_player)
    _player = import("@banuba/webar")
      .then(({ Player }) => Player.create(PlayerConfig))
      .then((player) => (Logger.debug("Player loaded"), player))

  return _player
}

let _preloading: Promise<void> | null = null
export const preload = async () => {
  if (_preloading) return _preloading

  Logger.debug("Starting preloading of analysis resources")

  await (_preloading = Resources.preload(
    Resources.SDK,
    Resources.AnalysisResources,
    Resources.AnalysisEffect
  ).catch((error) => {
    _preloading = null
    throw error
  }))

  Logger.debug("Analysis resources preloaded")

  if (isMultiThreaded) await start()
}

let _isEffectApplyInProgress = false
export const start = async () => {
  if (_isEffectApplyInProgress) return
  else _isEffectApplyInProgress = true

  Logger.debug("Starting analysis")

  const tasks = []

  tasks.push(getPlayer())

  if (!_modules) tasks.push(Module.preload(Resources.AnalysisResources))
  if (!_effect) tasks.push(Effect.preload(Resources.AnalysisEffect))

  try {
    const [player, modules, effect] = (await Promise.all(tasks)) as [Player, Module[], Effect]

    if (modules)
      await player.addModule(...(_modules = modules)), Logger.debug("Analysis modules loaded")
    if (effect) (_effect = effect), Logger.debug("Analysis effect loaded")

    if (State.effect !== _effect) await player.applyEffect((State.effect = _effect))

    Logger.debug("Analysis effect applied")
  } finally {
    _isEffectApplyInProgress = false
  }
}

let _isEffectClearInProgress = false
export const stop = async () => {
  if (_isEffectClearInProgress) return
  else _isEffectClearInProgress = true

  try {
    const player = await getPlayer()

    if (State.effect) (State.effect = null), await player.clearEffect()

    Logger.debug("Analysis effect cleared")
  } finally {
    _isEffectClearInProgress = false
  }
}

export type AnalysisEvent = "face" | "lightning"

type EventListenerMap = {
  [type in AnalysisEvent]: Set<(event: any) => void>
}

const _listeners: EventListenerMap = {
  face: new Set(),
  lightning: new Set(),
}

export const addEventListener = (type: AnalysisEvent, listener: (event: any) => void) => {
  _listeners[type].add(listener)
}

export const removeEventListener = (type: AnalysisEvent, listener: (event: any) => void) => {
  if (_listeners[type].has(listener)) _listeners[type].delete(listener)
}

export const dispatchEvent = (type: AnalysisEvent, event: any) => {
  _listeners[type].forEach((listener) => listener(event))
}

let _lightCoeff = 1
const _frameDataEventListener = ({ detail: frameData }: { detail: FrameData }) => {
  const hasFace = frameData.get("frxRecognitionResult.faces.0.hasFace")
  const coeff = hasFace ? frameData.get("lightCorrection") : _lightCoeff

  _lightCoeff = coeff

  dispatchEvent("face", hasFace)
  dispatchEvent("lightning", coeff >= 0.8)
}

const _addFrameDataEventListener = async () => {
  const player = await getPlayer()
  player.addEventListener(FRAME_DATA_EVENT, _frameDataEventListener)
}

const _removeFrameDataEventListener = async () => {
  const player = await getPlayer()
  player.removeEventListener(FRAME_DATA_EVENT, _frameDataEventListener)
}

export const useWebcam = async () => {
  const webcam = Sources.get("webcam")
  if (webcam.active) webcam.cancelStop()

  // Temporary disable
  // if (State.source === webcam && webcam.active) return webcam

  const player = await getPlayer()
  player.use((State.source = webcam), { crop })

  // listen to frame data event only in case of using webcam
  _addFrameDataEventListener()

  Logger.debug("Using Webcam for analysis")

  return webcam
}

export const usePhoto = async (source: Blob) => {
  const image = Sources.get(source)
  if (State.source === image) return

  const player = await getPlayer()

  player.use(live((State.source = image)), { resize, crop })

  Logger.debug("Using Image for analysis", source)
}

let isListening = false

const visibilityChangeListener = async () => {
  const isActive = document.visibilityState === "visible"
  const webcam = Sources.get("webcam")

  if (State.source !== webcam) return

  if (!isActive) {
    webcam.stop()
  } else {
    await useWebcam()
    const player = await getPlayer()
    player.play()
  }
}

const _addVisibilityChangListener = () => {
  if (isListening) return
  document.addEventListener("visibilitychange", visibilityChangeListener)
  isListening = true
}

const _removeVisibilityChangListener = () => {
  if (!isListening) return
  document.removeEventListener("visibilitychange", visibilityChangeListener)
  isListening = false
}

export const render = async (container: HTMLElement | string) => {
  Logger.debug("Rendering analysis to container", container)

  const webcam = Sources.get("webcam")
  const player = await getPlayer()

  player.play({ pauseOnEmpty: !(State.source instanceof Image) })

  if (State.source === webcam) {
    // Wait for the new input frame to be processed and rendered by player so there is no flash of previous source frame
    await new Promise((resolve) =>
      player.addEventListener("framereceived", resolve, { once: true })
    )
    await new Promise((resolve) =>
      player.addEventListener("frameprocessed", resolve, { once: true })
    )
    await new Promise((resolve) =>
      player.addEventListener("framerendered", resolve, { once: true })
    )
  }

  _addVisibilityChangListener()
  Dom.render(player, container)
}

export const unmount = async (container: HTMLElement | string) => {
  const webcam = Sources.get("webcam")
  if (State.source === webcam) webcam.scheduleStop()

  const player = await getPlayer()
  if (isMultiThreaded) player.pause()
  Dom.unmount(container)

  _removeVisibilityChangListener()
  _removeFrameDataEventListener()

  State.lastSource = State.source

  Logger.debug("Analysis removed from container", container)
}

export const destroy = async () => {
  const player = await getPlayer()

  player.destroy()

  Logger.debug("Player has been destroyed")
}

/**
 * Force-casts "static" image input to a "live" one.
 *
 * Danger zone: the code below highly leverages knowledge of WebAR SDK internals
 */
const live = (input: Image) => {
  return {
    async *[Symbol.asyncIterator](opts: any) {
      const frames = input[Symbol.asyncIterator](opts)
      const { value } = (await frames.next()) as IteratorResult<PixelsFrame, void>

      const { buffer, byteOffset, byteLength } = value!.data

      const copy = Uint8Array.from(new Uint8Array(buffer, byteOffset, byteLength))

      // original one
      yield {
        ...value!,
        input: "live",
      }

      // 2 more copies
      for (let i = 0; i < 2; ++i) {
        const data = opts.allocate(copy.length)

        data.set(copy)

        yield {
          ...value,
          data,
          input: "live",
        }
      }
    },
  } as Image
}
