import type { ComponentType } from "svelte"
import type { Paths } from "@lib/router"
import { interopDefault, join, normalize } from "./utils"

export type Redirect<T = unknown> = { redirect: T }
export type ComponentModule = { default: ComponentType }
export type ComponentModuleWithLoad = ComponentModule & { load: () => Promise<void> }

export type ActionReturn<R = unknown> =
  | void
  | Redirect<R>
  | ComponentType
  | ComponentModule
  | ComponentModuleWithLoad

export type Action<C = unknown, R = unknown> = (
  context: C
) => ActionReturn<R> | Promise<ActionReturn<R>>

export type RouteConfig<C = unknown, R = unknown> = {
  /**
   * The route's path
   * @default ""
   */
  path?: string
  name?: string
  /**
   * The route's component loader or simple action
   * @default `undefined`
   */
  action?: Action<C, R>
  /**
   * The route's child routes
   * @default `undefined`
   */
  children?: RouteConfig<C, R>[]
}

export type ResolvedRoute = {
  pathname: Paths
  components: ComponentType[]
}

export const createResolve = <C = unknown, R = unknown>(configs: RouteConfig<C, R>[]) => {
  const routes = createRoutesLookup(configs)

  const loaders = new Map<ComponentModuleWithLoad, ReturnType<ComponentModuleWithLoad["load"]>>()

  return async (pathname: string, context: C) => {
    const route = routes[pathname]
    if (!route) return

    const components = []

    for (const action of route.actions) {
      const ret = await action(context)

      if (ret == null) continue // `null` or `undefined`
      if ("redirect" in ret) return ret
      if ("load" in ret) {
        // allows a svelte component to export `load` method from <script context="module">
        if (!loaders.has(ret)) loaders.set(ret, ret.load())

        await loaders.get(ret)
      }

      components.push(interopDefault(ret))
    }
    return {
      pathname,
      components,
    }
  }
}

type Route<C = unknown, R = unknown> = {
  path: string
  actions: Action<C, R>[]
}

const createRoutesLookup = <C = unknown, R = unknown>(configs: RouteConfig<C, R>[]) => {
  const lookup: Record<string, Route<C, R>> = {}

  for (const { path = "", action = () => {}, children = [] } of configs) {
    const route: Route<C, R> = {
      path: normalize(path),
      actions: [action],
    }

    lookup[route.path] = route

    const childrenLookup = createRoutesLookup(children)

    for (const child of Object.values(childrenLookup)) {
      child.path = join(route.path, child.path)
      child.actions = route.actions.concat(child.actions)
      lookup[child.path] = child
    }
  }

  return lookup
}
