<!--
  The commponent abstracts connection of custom .css files:
    - the styles are attached to `documet.head, if the app is rendered to Ligh DOM
    - the styles are attached to `shadowRoot.host`, if the app is rendered to Shadow DOM
-->
<script lang="ts">
  import { onMount } from "svelte"
  import { getSvelteRoot } from "../utils"

  export let href: string
  /** Defines if the stylesheet is fonts-only stylesheet */
  export let fonts: boolean = /fonts.*\.css/.test(href)

  const svelteRoot = getSvelteRoot()
  const htmlRoot = svelteRoot.getRootNode() as Document | ShadowRoot

  const head =
    htmlRoot.nodeType === Node.DOCUMENT_NODE
      ? (htmlRoot as Document).head
      : // There is an [issue](https://stackoverflow.com/a/60526280), that the `@font-face` directives may not work insidethe the ShadowRoot.
      // A common workaround is to add the styles to the [shadownRoot.host](https://developer.mozilla.org/en-US/docs/Web/API/ShadowRoot/host) rather than `shadowRoot` itself.
      fonts
      ? (htmlRoot as ShadowRoot).host
      : (htmlRoot as ShadowRoot)

  onMount(() => {
    const link = head.appendChild(document.createElement("link"))
    link.rel = "stylesheet"
    link.href = href

    return () => link.remove()
  })
</script>
