<script lang="ts">
  import Icon, { type Variant as IconVariant } from "../Icon"
  import { getOpen } from "./context"

  export let as = "a"
  export let active = false
  export let icon: IconVariant | string = ""

  const ctx = getOpen()

  $: open = $ctx
  $: [src, variant] = /\.svg/.test(icon) ? [icon] : [, icon as IconVariant]
</script>

<svelte:element
  this={as}
  class="tint-navbar-item"
  class:active
  role="button"
  on:click|preventDefault
  {...$$restProps}
>
  {#if src}
    <Icon {src} />
  {:else if variant}
    <Icon {variant} />
  {/if}
  <span class:hidden={!open}>
    <slot />
  </span>
</svelte:element>

<style lang="postcss">
  .tint-navbar-item {
    @apply flex h-full w-full basis-0 items-center whitespace-nowrap rounded-lg bg-white bg-opacity-0 p-2 text-sm font-semibold opacity-75 transition-colors;

    &:hover {
      @apply bg-opacity-[0.07] opacity-100;
    }
    &.active {
      @apply bg-opacity-[0.15] opacity-100;
    }
    &.active:hover,
    &:active {
      @apply bg-opacity-20 opacity-100;
    }

    & > :global(*) {
      @apply flex-shrink-0;
    }
  }

  span {
    @apply ml-2 inline transition-opacity duration-200;

    &.hidden {
      @apply opacity-0 delay-[0s] duration-300;
    }
  }
</style>
