<!-- Replaces native browser tooltips derived from "title" attributes with custom ones -->
<script lang="ts">
  import { onMount } from "svelte"
  import type { SvelteComponent } from "svelte/internal"
  import type Floating from "../Floating"
  import { getSvelteRoot, media } from "../utils"

  const isHoverSupported = media("(any-hover: hover)")
  const root = getSvelteRoot()

  let FloatingComponent: typeof Floating
  let tooltip: Floating
  let anchor: HTMLElement | null = null
  let title = ""

  $: tooltip && (anchor ? tooltip.open(anchor) : tooltip.close())

  onMount(() => {
    if (!$isHoverSupported) return

    import("../Floating").then((m) => (FloatingComponent = m.default))
  })

  onMount(() => {
    if (!$isHoverSupported) return

    const hover = ({ target }: MouseEvent) => {
      let el = target as HTMLElement | null

      while (el && !el.title) {
        if (el === anchor) return
        el = el.parentElement
      }

      if (el && el.title) {
        anchor = el
        title = el.title
        el.title = ""
      } else {
        if (anchor) anchor.title = title
        anchor = null
      }
    }

    root.addEventListener("pointerover", hover)
    root.addEventListener("pointerleave", hover)

    return () => {
      root.removeEventListener("pointerover", hover)
      root.removeEventListener("pointerleave", hover)
    }
  })
</script>

<svelte:component this={FloatingComponent} bind:this={tooltip} offset={[4, 0]}>
  <span>{title}</span>
</svelte:component>

<style lang="postcss">
  span {
    @apply rounded bg-[#38383c] px-3 py-1 text-xs text-white;
  }
</style>
