import { get_current_component } from "svelte/internal"

let root: HTMLElement | undefined

/**
 * @private
 * Designed to be used only for testing purpose
 */
export const setSvelteRoot = (el: HTMLElement) => (root = el)

/**
 * Returns the DOM element at which the Svelte app is mounted.
 *
 * Commonly used as a mounting for floating elements like popups, tooltips, drawers, etc
 */
export const getSvelteRoot = () => {
  // https://github.com/sveltejs/svelte/blob/master/src/runtime/internal/Component.ts#L100
  return (
    (root as HTMLElement & { host: never }) ||
    (get_current_component().$$.root as (HTMLElement & { host: never }) | ShadowRoot)
  )
}

export const getWindow = () => getSvelteRoot().ownerDocument.defaultView!
