export const resize = (width: number, height: number): [number, number] => {
  console.debug("__initial w, h:", width, height)

  const screenW = screen.width * devicePixelRatio
  const screenH = screen.height * devicePixelRatio

  if (width <= screenW && height <= screenH) return [width, height]
  const maxRatio = Math.max(width / screenW, height / screenH)

  console.debug("__after resize w, h:", Math.round(width / maxRatio), Math.round(height / maxRatio))
  return [Math.round(width / maxRatio), Math.round(height / maxRatio)]
}
